<template>
	<view class="content">
		<view v-if="policeList.length > 0">
			<uni-card :is-shadow="false" is-full><uni-data-checkbox mode="list" multiple v-model="checked" :localdata="policeList"></uni-data-checkbox></uni-card>
			<view class="flex flex-direction" style="padding:30rpx"><button class="cu-btn bg-blue lg" @click="confirm">确 定</button></view>
		</view>
		<view class="empty-box" v-else>
			<image src="@/static/img/empty-image.png" class="empty-image"></image>
			<view class="empty-text">暂无数据</view>
		</view>
	</view>
</template>

<script>
import { taskAPI } from '@/api/index.js';
export default {
	data() {
		return {
			checked: [],
			policeList: [],
			id: '',
			oldGridPolice: ''
		};
	},
	onLoad(options) {
		console.log(options);
		this.id = options.id;
		this.oldGridPolice = options.oldGridPolice;
		if (this.id && this.oldGridPolice) {
			this.getAgainAssignGrid();
		}
	},
	methods: {
		getAgainAssignGrid() {
			let param = {
				id: this.id,
				oldGridPolice: this.oldGridPolice
			};
			taskAPI.getAgainAssignGrid(param).then(res => {
				if (res.code == 200) {
					this.policeList = res.data.map(item => {
						return {
							text: item.value,
							value: item.key
						};
					});
				}
			});
		},

		confirm() {
			if(this.checked.length==0){
				uni.showToast({
					icon: 'none',
					title: '请选择派发对象'
				});
				return
			}
			uni.showModal({
				content: '确定重新派发吗？',
				success: rs => {
					if (rs.confirm) {
						let param = {
							id: this.id,
							oldGridPolice: this.oldGridPolice,
							newGridPolice:this.checked.join(',')
						};
						console.log(param)
						taskAPI.againAssign(param).then(res => {
							if (res.code == 200) {
								uni.showToast({
									icon: 'none',
									title: '重新派发成功'
								});
								uni.navigateBack({delta: 2});
							}
						});
					}
				}
			});
		}
	}
};
</script>

<style></style>
